@import url(https://fonts.googleapis.com/css2?family=VT323&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
/*! minireset.css v0.0.6 | MIT License | github.com/jgthms/minireset.css */
html,
body,
p,
ol,
ul,
li,
dl,
dt,
dd,
blockquote,
figure,
fieldset,
legend,
textarea,
pre,
iframe,
hr,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

ul {
  list-style: none;
}

button,
input,
select,
textarea {
  margin: 0;
  background: none;
  border: none;
  padding: 0;
  outline: 0;
}



html {
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

img,
video {
  height: auto;
  max-width: 100%;
}





td:not([align]),
th:not([align]) {
  text-align: left;
}


/* hide scrollbar */
/* Hide scrollbar for Chrome, Safari and Opera */
body::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
body {
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
}

.link {
  text-decoration: none;
}

.header {
    height: 10vh;
    background-color: black;
    justify-content: flex-end;
    padding-right: 3vw;
    display: flex;
}

.header .nav {
    display: flex;
    align-items: flex-end;

}

.header .toggle {

    
}

.header .menu-button {
    font-size: 50px;
    height: 50px;
    color: lightgrey;

}
.header .nav-link {
    color: lightgray;
    font-family: 'VT323', monospace; 
    font-size: 30px;
    margin-right: 3vw;
    padding: 5px;
}

.nav-link:hover {
    color: white;
    cursor: pointer;
}
.game {
    display: flex;
    justify-content: center;
    max-width: 1024px;
    margin: auto;

}

.cell:hover {
    border: 1px solid grey;
    cursor: pointer;
}
.grid-wrapper {
    display: flex;
    flex-direction: row;
    
}

.cols-wrapper {
    display: flex;
    flex-direction: column;
}

.game-menu {
    position: fixed;
    left: 5vw;
    bottom: 5vh;
    display: flex;
}

.game .menu-button {
    color: lightgrey;
    font-size: 40px;
    height: 62px;
}

.game .toggle {
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.game .intro {
    color: grey;
    font-family: 'VT323', monospace; 
    font-size: 30px;
    padding-left: 2vw;

}

.game .actions {
    padding-left: 2vw;
    display: flex;
}

.actions button,
.actions p {
    color: lightgray;
    font-family: 'VT323', monospace; 
    font-size: 30px;
    margin-right: 2vw;
}

.actions .speed-button {
    padding-left: 4px;
    padding-right: 4px;
    margin-right: 0;
}

.menu-button:hover,
.actions button:hover,
.intro:hover {
    color: white;
    cursor: pointer;
    
}

/* mobile */
@media (max-width: 400px) {
    .actions button, .actions p, .game .intro {
        font-size: 20px;
    }
}

.project-content {
    background-color: black;
    min-height: 90vh;
    color: white;
}


